import * as R from 'ramda'
import React, { Fragment } from 'react'
import styled, { injectGlobal } from 'react-emotion'
import ScaleLoader from 'react-spinners/ScaleLoader'
import Component from '@reach/component-component'
import { Dialog, DialogOverlay, DialogContent } from '@reach/dialog'

injectGlobal`
  [data-reach-dialog-overlay] {
    background: hsla(0, 0%, 0%, 0.5);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
  }

  [data-reach-dialog-content] {
    position: relative;
    width: 50vw;
    margin: 10vh auto;
    background: white;
    padding: 2rem;
    outline: none;
    color: black;
  }
`

const MARGIN = '50px'

const Loading = styled('div')`
  margin-top: ${MARGIN};
  text-align: center;
  font-size: 30px;
`

const Wrapper = styled('div')`
  margin-top: ${MARGIN};
`

const ItemWrapper = styled('div')`
  display: flex;
  align-items: center;
  width: 100%;
  min-width: 320px;
  border: 1px dashed white;
  border-radius: 5px;
`

const Name = styled('div')`
  flex: 1;
  padding: 20px;
`

const Version = styled('span')`
  opacity: 0.5;
`

const LicenseList = styled('div')`
  border-left: 1px dashed white;
  color: deeppink;
  padding: 20px;

  a {
    outline: none;
    color: deeppink;
    text-decoration: none;
  }
`

const getLicensesName = R.pipe(
  R.path(['licenseSources', 'package', 'sources']),
  R.defaultTo([]),
  R.map(R.prop('license')),
)

const Item = ({ item }) => {
  const name = R.prop('name', item)
  const version = R.prop('version', item)
  const licenses = getLicensesName(item)

  return (
    <ItemWrapper>
      <Name>
        {name}
        <Version>@{version}</Version>
      </Name>
      <Component initialState={{ showDialog: false }}>
        {({ state, setState }) => (
          <LicenseList>
            {licenses.map(license => (
              <Fragment>
                <a href="#" onClick={() => setState({ showDialog: true })}>
                  {license}
                </a>
                <Dialog isOpen={state.showDialog}>
                  <button
                    className="close-button"
                    onClick={() => setState({ showDialog: false })}
                  >
                    <span aria-hidden>Ã—</span>
                  </button>
                  <p>Hello there. I am a dialog</p>
                </Dialog>
              </Fragment>
            ))}
          </LicenseList>
        )}
      </Component>
    </ItemWrapper>
  )
}

export const Licenses = ({ isLoading, error, data }) => {
  if (isLoading) {
    return (
      <Loading>
        <ScaleLoader size={120} color="white" loading />
      </Loading>
    )
  }

  return (
    <Wrapper>
      <Item
        item={{
          name: 'react',
          version: '1',
          licenseSources: {
            package: {
              sources: [
                {
                  license: 'MIT',
                  url: '(none)',
                },
              ],
            },
          },
        }}
      />
      {data && data.length > 0 && data.map(item => <Item item={item} />)}
    </Wrapper>
  )
}



